.search-form input,
.search-btn,
.search-btn:before,
.search-btn:after {
  transition: all 0.25s ease-out;
}

.search-form input,
.search-btn {
  width: 2.4em;
  height: 2.4em;
}

.search-form input:invalid:not(:focus),
.search-btn {
  cursor: pointer;
}

.search-form,
.search-form input:focus,
.search-form input:valid {
  width: 60%;
}

.search-form input:focus,
.search-form input:not(:focus)+.search-btn:focus {
  outline: transparent;
}

.search-form {
  margin: 0 auto;
  padding: 0.2em 0 0.4em 0;
  justify-content: center;
  max-width: 30em;
}

// .current {
  
// }

.search-form input {
  background: transparent;
  border-radius: 1.5em;
  box-shadow: 0 0 0 0.4em #171717 inset;
  padding: 0.6em 0.6em 0.6em 1.8em;
  transform: translate(0.5em, 0.5em) scale(0.5);
  transform-origin: 100% 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.search-form input::-webkit-search-decoration {
  -webkit-appearance: none;
}

.search-form input:focus,
.search-form input:valid {
  background: #fff;
  border-radius: 0.375em 0 0 0.375em;
  box-shadow: 0 0 0 0.1em #d9d9d9 inset;
  transform: scale(1);
}

.search-btn {
  background: #171717;
  border-radius: 0 0.75em 0.75em 0 / 0 1.5em 1.5em 0;
  padding: 0.75em;
  position: relative;
  transform: translate(0.25em, 0.25em) rotate(45deg) scale(0.25, 0.125);
  transform-origin: 0 50%;
}

.search-btn:before,
.search-btn:after {
  content: "";
  display: block;
  opacity: 0;
  position: absolute;
}

.search-btn:before {
  border-radius: 50%;
  box-shadow: 0 0 0 0.15em #f1f1f1 inset;
  top: 0.6em;
  left: 0.7em;
  width: 1em;
  height: 1em;
}

.search-btn:after {
  background: #f1f1f1;
  border-radius: 0 0.25em 0.25em 0;
  top: 53%;
  left: 51%;
  width: 0.7em;
  height: 0.18em;
  transform: translate(0.2em, 0) rotate(45deg);
  transform-origin: 0 50%;
}

.search-btn span {
  display: inline-block;
  overflow: hidden;
  width: 1px;
  height: 1px;
}

/* Active state */
.search-form input:focus+.search-btn,
.search-form input:valid+.search-btn {
  background: #4186fea0;
  border-radius: 0 0.375em 0.375em 0;
  transform: scale(1);
}

.search-form input:focus+.search-btn:before,
.search-form input:focus+.search-btn:after,
.search-form input:valid+.search-btn:before,
.search-form input:valid+.search-btn:after {
  opacity: 1;
}

.search-form input:focus + .search-btn:hover,
.search-form input:valid + .search-btn:hover {
  // background: #4202f4;
  background: linear-gradient(45deg,
    #ff00c8,
    #7b00ff,
    #002aff,
    #7b00ff,
    #ff00c8);
}

.search-form input:focus+.search-btn:active,
.search-form input:valid+.search-btn:active {
  transform: translateY(1px);
}

@media screen and (prefers-color-scheme: dark) {
  body,
  input {
    color: #f1f1f1;
  }

  body {
    background: #171717;
  }

  .search-form input {
    box-shadow: 0 0 0 0.4em #f1f1f1 inset;
  }

  .search-form input:focus,
  .search-form input:valid {
    background: #292929;
    box-shadow: none;
  }

  .search-btn {
    background: #f1f1f1;
  }
}